@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
:root {
	--main-accent: #226622;
	--main-accent-compliment: #ccc;
	--main-accent-darker: #113311;
	--main-background-color: #252525;
	--max-screen-width-1: 486px;
	--max-screen-width-2: 540px;
	--max-screen-width-3: 768px;
	--max-screen-width-4: 940px;
	--main-font-family: 'Muli';
	--main-transition-duration: 0.25s;
}

* {
	box-sizing: border-box;
}

html {
	font-family: 'Muli', sans-serif;
}

body {
	margin: 0;
	background-color: #252525;
	color: #ccc;
	color: var(--main-accent-compliment);
}

.header .search-input {
	font-size: 1.2rem;
	padding: 1rem 1.5rem;
	border: none;
	background-color: var(--main-accent-darker);
	opacity: 0.5;
	color: #fff;
	width: 15rem;
	border-radius: 5rem;
}

.header .search-input:focus {
	outline: none;
}

.header .search-input-btn {
	height: 80%;
	padding: 0 1rem;
	border-radius: 5rem;
	background-color: transparent;
	color: #ccc;
	border: none;
	cursor: pointer;
	position: absolute;
	top: 50%;
	right: 2.5%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	transition: background-color var(--main-transition-duration) ease;
}

.header .search-input-btn:hover {
	background-color: var(--main-accent-darker);
}

.header .search-input-btn .fa {
	font-size: 1rem;
}

/* Imported fonts: Architects Daughter, Indie Flower, Kaushan Script */
/* @import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Indie+Flower&family=Kaushan+Script&display=swap'); */

.header {
	background-color: var(--main-accent);
	color: #fff;
	margin-top: 0;
	display: -webkit-flex;
	display: flex;
	-webkit-align-content: center;
	        align-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-filter: drop-shadow(2px 4px 6px #0008);
	        filter: drop-shadow(2px 4px 6px #0008);
}

.header .title {
	font-family: 'Kaushan Script', var(--main-font-family);
	letter-spacing: 0.25rem;
}

.header form {
	width: 15rem;
	position: relative;
}

@media (max-width: 486px) {
	.header {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-align-items: center;
		        align-items: center;
	}
	.header form {
		margin-bottom: 1.5rem;
	}
}

.card {
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
	border-radius: 0.75rem;
	background-color: #181818;
}

.card.with-padding {
	padding: 1rem;
}

.badge {
	display: inline-block;
	background-color: var(--main-accent);
	color: var(--main-accent-compliment);
	font-weight: bold;
	/* font-family: 'courier new'; */
	letter-spacing: 0.1rem;
	padding: 0.5rem 0.75rem;
	margin: 0.25rem;
	max-width: 85%;
	border-radius: 0.25rem;
}

@media (max-width: 620px) {
	.badge {
		letter-spacing: 0.01rem;
		padding: 0.25rem 0.55rem;
		margin: 0.2rem 0.1rem;
		font-weight: normal;
	}
}

:root {
	--veg-accent: #080;
	--non-veg-accent: #800;
	--egg-accent: #880;
}

.food-type-mark {
	width: 1rem;
	height: 1rem;
	border-radius: 0.25rem;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	position: absolute;
	bottom: 1rem;
	right: 1rem;
}

.food-type-mark .inner-circle {
	width: 0.5rem;
	height: 0.5rem;
	border-radius: 50%;
}

.food-type-mark.veg {
	border: 1.5px solid #080;
	border: 1.5px solid var(--veg-accent);
}

.food-type-mark.veg .inner-circle {
	background-color: #080;
	background-color: var(--veg-accent);
}

.food-type-mark.non-veg {
	border: 1.5px solid #800;
	border: 1.5px solid var(--non-veg-accent);
}

.food-type-mark.non-veg .inner-circle {
	background-color: #800;
	background-color: var(--non-veg-accent);
}

.food-type-mark.egg {
	border: 1.5px solid #880;
	border: 1.5px solid var(--egg-accent);
}

.food-type-mark.egg .inner-circle {
	background-color: #880;
	background-color: var(--egg-accent);
}

.food-type-mark.loading {
	border: 1.5px solid var(--main-background-color);
}

.food-type-mark.loading .inner-circle {
	background-color: var(--main-background-color);
}

@media (max-width: 620px) {
	.food-type-mark {
		bottom: 0.5rem;
		right: 0.5rem;
	}
}

.recipe-list-item {
	height: 20rem;
	/* height: 100%; */
	cursor: pointer;
	transition: all 0.1s ease;
	position: relative;
	overflow: overlay;
}

.recipe-list-item:hover {
	-webkit-transform: scale(1.01);
	        transform: scale(1.01);
}

.recipe-list-item:hover .recipe-list-item__img {
	-webkit-filter: brightness(1);
	        filter: brightness(1);
}

.recipe-list-item__img {
	height: 50%;
	background-position: center;
	background-size: cover;
	-webkit-filter: brightness(0.75);
	        filter: brightness(0.75);
	transition: -webkit-filter var(--main-transition-duration);
	transition: filter var(--main-transition-duration);
	transition: filter var(--main-transition-duration), -webkit-filter var(--main-transition-duration);
	box-shadow: inset 0 0 16px 0px #000;
}

.img_loader {
	height: 50%;
	box-shadow: inset 0 0 16px 0px #111;
}

.recipe-list-item-details {
	padding: 1rem;
}

.recipe-list-item__title {
	margin: 0;
	font-size: 1.2rem;
}

.recipe-list-item__title {
	width: 100%;
	height: 1.5rem;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.recipe-list-item__tags-container {
	margin-top: 1rem;
	max-height: 5.5rem;
	border-radius: 0.25rem;
	overflow: overlay;

	/* To hide the scrollbar */
	-ms-overflow-style: none;
	scrollbar-width: none;
}

.recipe-list-item__tags-container::-webkit-scrollbar {
	display: none;
}

/* Loaders */
.recipe-name-loader {
	background-color: var(--main-background-color);
	font-weight: bold;
	/* font-family: 'courier new'; */
	letter-spacing: 0.1rem;
	padding: 0.75rem;
	margin: 0.25rem;
	max-width: 85%;
	border-radius: 0.25rem;
}

.recipe-tag-loader {
	display: inline-block;
	background-color: var(--main-background-color);
	font-weight: bold;
	/* font-family: 'courier new'; */
	letter-spacing: 0.1rem;
	padding: 0.9rem;
	margin: 0.25rem;
	max-width: 85%;
	border-radius: 0.25rem;
}

.recipe-tag-loader.tag-1 {
	width: 30%;
}
.recipe-tag-loader.tag-2 {
	width: 40%;
}
.recipe-tag-loader.tag-3 {
	width: 50%;
}

.loader {
	position: relative;
	overflow: hidden;
}

.loader::before {
	content: '';
	position: absolute;
	top: 10%;
	left: -100%;
	height: 50rem;
	-webkit-transform: translateY(-50%) rotate(-10deg);
	        transform: translateY(-50%) rotate(-10deg);
	/*width:2.5rem;*/
	background-color: #aaa;
	box-shadow: 0 0 24px 2px #fff8;
}

.img_loader::before {
	box-shadow: 0 0 72px 2px #fff8;
}

.loading .loader::before {
	-webkit-animation: loading-reflection 1.5s infinite;
	        animation: loading-reflection 1.5s infinite;
}

@media (max-width: 620px) {
	.recipe-list-item {
		height: 5rem;
		display: -webkit-flex;
		display: flex;
		overflow-x: overlay;
	}
	.recipe-list-item__img {
		height: 100%;
		width: 25%;
	}
	.recipe-list-item-details {
		padding: 0.5rem;
		width: 75%;
	}
	.recipe-list-item__title {
		display: inline-block;
		width: 100%;
		font-size: 1.2rem;
		height: 1.5rem;
	}
	.img_loader {
		height: 100%;
		width: 25%;
	}
	.recipe-list-item__tags-container {
		margin-top: 0;
	}
	.recipe-list-item__tags-container .recipe-tag {
		display: none;
	}
	.recipe-list-item__tags-container .recipe-tag:nth-child(1) {
		display: inline-block;
	}
	.recipe-list-item__tags-container .recipe-tag:nth-child(2) {
		display: inline-block;
	}
	.recipe-list-item__tags-container .recipe-tag:nth-child(3) {
		display: inline-block;
	}

	.img_loader::before {
		box-shadow: 0 0 24px 2px #fff8;
	}
}

@media (max-width: 480px) {
	.recipe-list-item__tags-container .recipe-tag:nth-child(3) {
		display: none;
	}
}

@-webkit-keyframes loading-reflection {
	0% {
		left: -50%;
	}
	100% {
		left: 150%;
	}
}

@keyframes loading-reflection {
	0% {
		left: -50%;
	}
	100% {
		left: 150%;
	}
}

.recipes-list {
	margin: 5rem;
	list-style-type: none;
	padding: 0;

	/* For large screens */
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-column-gap: 2rem;
	-webkit-column-gap: 2rem;
	        column-gap: 2rem;
	grid-row-gap: 2rem;
	row-gap: 2rem;
	/* grid-auto-rows: minmax(20rem, auto); */
}

@media (max-width: 1080px) {
	.recipes-list {
		grid-template-columns: repeat(3, 1fr);
	}
}

@media (max-width: 840px) {
	.recipes-list {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media (max-width: 620px) {
	.recipes-list {
		margin: 5rem 2rem;
		/* For extra small screens */
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
	}
}

